.container {
    margin-bottom: 45px;
    text-align: center;
}

.search-form {
    background-color: #0269a4;
    width: 100%;
    padding-top: 10px;
    padding-bottom: 10px;
    text-align: center;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    position: fixed;
    top: 0;
    left: 0;
}

.gallery {
    margin-top: 45px;
    display: flex;
    flex-wrap: wrap;
    width: 83%;
    margin-bottom: 10px;
    margin-left: auto;
    margin-right: auto;
    justify-content: center;
    align-items: center;
}

.photo-card {
    flex-basis: 20%;
    border: 1px solid black;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    margin: 5px;
    border-radius: 2px;
}

.load-more {
    display: none;
    border: none;
    background-color: #0269a4;
    font-size: 16px;
    color: white;
    padding: 12px;
    border-radius: 6px;
    text-align: center;
    cursor: pointer;
    min-width: 150px;
    min-height: 48px;

  
}

  .load-more:hover {
      box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
      opacity: .9;
  }

.load-more.visible {
      display: block;
      margin-left: auto;
      margin-right: auto;

      
  }

 .load-more.visible .load-more__wait {
      display: none;
  }

  .load-more.visible.loading .load-more__more{       
    display: none;
}

.load-more.visible.loading .load-more__wait {
      display: flex;
      align-items: center;
      justify-content: center;
  }

.info {
    display: flex;

}

.info-item {
    margin-top: 5px;
    margin-bottom: 5px;
    padding: 5px;
    display: block;
    text-align: center;
}
.info-item b {
    display: block;
}

.link {
    display: block;
    width: 300px;
    height: 200px;
}

.img {
    display: block;
    height: 100%;
    width: 100%;
    object-fit: cover;
}

/* // spinner */
.loader {
    font-size: 10px;
    margin: 6px;
    text-indent: -9999em;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background: #ffffff;
    background: -moz-linear-gradient(left, #ffffff 10%, rgba(255, 255, 255, 0) 42%);
    background: -webkit-linear-gradient(left, #ffffff 10%, rgba(255, 255, 255, 0) 42%);
    background: -o-linear-gradient(left, #ffffff 10%, rgba(255, 255, 255, 0) 42%);
    background: -ms-linear-gradient(left, #ffffff 10%, rgba(255, 255, 255, 0) 42%);
    background: linear-gradient(to right, #ffffff 10%, rgba(255, 255, 255, 0) 42%);
    position: relative;
    -webkit-animation: load3 1.4s infinite linear;
    animation: load3 1.4s infinite linear;
    -webkit-transform: translateZ(0);
    -ms-transform: translateZ(0);
    transform: translateZ(0);
}

.loader:before {
    width: 50%;
    height: 50%;
    background: #ffffff;
    border-radius: 100% 0 0 0;
    position: absolute;
    top: 0;
    left: 0;
    content: '';
}

.loader:after {
    background: #0269a4;
    width: 75%;
    height: 75%;
    border-radius: 50%;
    content: '';
    margin: auto;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}

@-webkit-keyframes load3 {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

@keyframes load3 {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}